@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@300;400;500;600&display=swap");
:root body {
  --almarenamonodisplaybold: "almarenamonodisplaybold";
  --almarenamonodisplaylight: "almeromonodisplaylight";
  --almarenamonofwlight: "almarenamonofwlight";
  --almarenamonolight: "almarenamonolight";
  --gilroyregular:"gilroyregular";
  --gilroymedium:"gilroymedium";
  --gilroysemibold: "gilroysemibold";
  --verlagbold: "verlagbold";
}

@font-face {
  font-family: "almarenamonodisplaybold";
  src: url("../fonts/Almarena-Mono-Complete-Family/AlmarenaAlmarenaMonoDisplayBold.otf");
  font-display: swap;
}
@font-face {
  font-family: "almarenamonolight";
  src: url("../fonts/Almarena-Mono-Complete-Family/AlmarenaAlmarenaMonoLight.otf");
  font-display: swap;
}
@font-face {
  font-family: "almarenamonofwlight";
  src: url("../fonts/Almarena-Mono-Complete-Family/AlmarenaAlmarenaMonoFWDisplayLight.otf");
  font-display: swap;
}
@font-face {
  font-family: "almarenamonodisplaylight";
  src: url("../fonts/Almarena-Mono-Complete-Family/AlmarenaAlmarenaMonoDisplayLight.otf");
  font-display: swap;
}
@font-face {
  font-family: "gilroymedium";
  src: url("../fonts/gilroy/Gilroy-Medium.ttf");
  font-display: swap;
}
@font-face {
  font-family: "gilroysemibold";
  src: url("../fonts/gilroy/Gilroy-SemiBold.ttf");
  font-display: swap;
}
@font-face {
  font-family: "gilroyregular";
  src: url("../fonts/gilroy/Gilroy-Regular.ttf");
  font-display: swap;
}
@font-face {
