/* ---------- Container for the orbiting circles animation ---------- */
#circle-orbit-container {
  position: relative;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  height: 100vh;
  width: 100%;
}

/* ---------- Inner orbit - This is the circles closest to the central point ---------- */
#inner-orbit {
  z-index: 1058;
  position: absolute;
  /*top: 75px;
  left: 75px;*/
  left: calc(50% - 30vh);
  top: calc(50% - 30vh);
  transform: translate(-50%);
  width: 60vh;
  height: 60vh;
  border: 0.45px #2C2E45 solid;
  border-radius: 100%;
  transition: all 3ms ease-in-out;
  -webkit-transform: rotate(360deg);
  transform: rotate(360deg);
}

/* ---------- Repeating styles for the inner orbiting circles ---------- */
.inner-orbit-element1 {
  position: absolute;
  top: 5vh;
  left: 60vh;
  cursor: pointer;
  width: 40vh;
  z-index: 1058;
  transition: all 300ms ease-in-out;
  transform: scale(1);
}
.inner-orbit-element1:hover {
  transform: scale(1.02);
  -webkit-transform: scale(1.02);
}
@media screen and (max-width: 1550px) {
  .inner-orbit-element1 {
    width: 30vh;
  }
}
@media screen and (max-width: 1450px) {
  .inner-orbit-element1 {
    width: 25vh;
  }
}

.inner-orbit-element2 {
  position: absolute;
  top: 0vh;
  right: 60vh;
  width: 40vh;
  transition: all 0.3s ease-in-out;
  transform: scale(1);
}
.inner-orbit-element2:hover {
  transform: scale(1.02);
  -webkit-transform: scale(1.02);
}
@media screen and (max-width: 1550px) {
  .inner-orbit-element2 {
    width: 30vh;
  }
}
@media screen and (max-width: 1450px) {
  .inner-orbit-element2 {
    width: 25vh;
  }
}

.inner-orbit-element3 {
  position: absolute;
  top: 50vh;
  left: 15vh;
  width: 30vh;
  transition: all 0.3s ease-in-out;
  transform: scale(1);
}
.inner-orbit-element3:hover {
  transform: scale(1.02);
  -webkit-transform: scale(1.02);
}
@media screen and (max-width: 1550px) {
  .inner-orbit-element3 {
    width: 20vh;
    left: 20vh;
    top: 60vh;
  }
}
@media screen and (max-width: 1450px) {
  .inner-orbit-element3 {
    width: 20vh;
    left: 20vh;
    top: 60vh;
  }
}

/* ---------- Middle orbit - This is the circles second closest to the central point ---------- */
#middle-orbit {
  position: absolute;
  left: calc(50% - 45vh);
  top: calc(50% - 45vh);
  transform: translate(-50%);
  width: 90vh;
  height: 90vh;
  border: 0.45px #2C2E45 solid;
  border-radius: 100%;
  -webkit-transform: rotate(360deg);
  z-index: -1;
}

/* ---------- Repeating styles for the inner orbiting circles ---------- */
/* ---------- Outer orbit - This is the circles furthest away from the central point ---------- */
#outer-orbit {
  position: absolute;
  left: calc(50% - 60vh);
  top: calc(50% - 60vh);
  transform: translate(-50%);
  width: 120vh;
  height: 120vh;
  border: 0.45px #2C2E45 solid;
  border-radius: 100%;
  -webkit-animation: spin-right 60s linear infinite;
  animation: spin-right 80s linear infinite;
  z-index: -1;
}

/* ---------- Repeating styles for the outer orbiting circles ---------- */
.outer-orbit-cirlces0 {
  display: block;
  height: 34px;
  width: 34px;
  margin: -17px;
  position: absolute;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  z-index: 1;
  transform: rotate(47deg) translate(-60vh) rotate(-47deg);
}

.outer-orbit-cirlces1 {
  display: block;
  height: 42px;
  width: 42px;
  margin: -21px;
  position: absolute;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  z-index: 1;
  transform: rotate(322deg) translate(-60vh) rotate(-322deg);
}

.outer-orbit-cirlces2 {
  display: block;
  height: 47px;
  width: 47px;
  margin: -23.5px;
  position: absolute;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  z-index: 1;
  transform: rotate(146deg) translate(-60vh) rotate(-146deg);
}

#extra-outer-orbit {
  position: absolute;
  left: calc(50% - 75vh);
  top: calc(50% - 75vh);
  transform: translate(-50%);
  width: 150vh;
  height: 150vh;
  border: 0.45px #2C2E45 solid;
  border-radius: 100%;
  -webkit-animation: spin-right 20s linear infinite;
  animation: spin-right 20s linear infinite;
  z-index: -1;
}

#extra-plus-outer-orbit {
  position: absolute;
  left: calc(50% - 90vh);
  top: calc(50% - 90vh);
  transform: translate(-50%);
  width: 180vh;
