  position: relative;
  width: 100%;
  padding: 60px 0;
}
.timeline .timeline-continue:after {
  position: absolute;
  content: "";
  width: 1px;
  height: 100%;
  top: 0;
  left: 50%;
  margin-left: -1px;
  background: #5156be;
}
