}
.timeline .timeline-right {
  text-align: left;
}
.timeline .timeline-right .timeline-icon {
  text-align: right;
}
.timeline .timeline-icon::after {
  content: "";
  display: block;
  position: absolute;
  width: 14px;
  height: 14px;
  top: 45px;
  background: #5156be;
  border-radius: 15px;
  z-index: 1;
