  margin: 15px;
  padding: 20px;
  border: 1px solid var(--bs-gray-200);
  border-radius: 6px;
}
.timeline .timeline-box::after {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  top: 26px;
  background-color: var(--bs-card-bg);
  border: 1px solid #e9e9ef;
  transform: rotate(45deg);
  margin: 0px auto;
}
.timeline .timeline-launch {
  position: relative;
  display: inline-block;
  margin: 15px;
  padding: 20px;
  border: 1px solid #e9e9ef;
  border-radius: 6px;
  width: 100%;
  margin: 15px 0;
  padding: 0;
  border: none;
  text-align: center;
  background: transparent;
}

.timeline-date {
  right: 20px;
}
@media (max-width: 767.98px) {
  .timeline-date {
    left: -70px;
    right: auto;
  }
}

.timeline-left .timeline-date {
  left: 20px;
}
@media (max-width: 767.98px) {
  .timeline-left .timeline-date {
    left: -70px;
    right: auto;
  }
}

.timeline .timeline-date {
  width: 54px;
  height: 80px;
  display: inline-block;
  padding: 8px;
  clip-path: polygon(0 0, 100% 0, 100% 80%, 50% 100%, 0 80%);
